import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { AkService } from '../ak.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  
  pass:any;
  getToken: any;
  message: any;
  name: string = 'prathmesh';
  password: string = '123456';
  user: any;
  constructor(private auth:AkService,public router:Router,route:ActivatedRoute ) { }

  ngOnInit(): void {
  }
  login() {
    let loginCredentials = {
      email: this.name,
      password: this.password,
    };
    console.log(loginCredentials);

    this.auth.login(loginCredentials).subscribe((res) => {
      if (res['success']) {
        this.user = res['data'];
        console.log(this.user);
        this.router.navigate(['home']);
      } else {
        alert('login failed');
      }
    });
  // login(){
    // console.log("id,pass",this.name,this.pass)
    // if(this.name && this.pass){
    // }
    // this.router.navigate(["/dashboard"]); 
  //  }

  }

}
